import Image from "next/image";

export default function LoginPage() {
  return (
    <div className="min-h-screen flex items-center justify-center">
      <div className="max-w-4xl w-full grid grid-cols-1 md:grid-cols-2 bg-white rounded-xl shadow-lg overflow-hidden">
        <div className="p-8 flex flex-col justify-center gap-6">
          <h1 className="text-3xl font-bold">Welcome Back</h1>
          <p className="text-sm text-gray-500">Login to access your HRMS dashboard</p>

          <div className="space-y-4">
            <input className="w-full border rounded px-4 py-2" placeholder="Email or Mobile" />
            <input type="password" className="w-full border rounded px-4 py-2" placeholder="Password" />

            <a href="/dashboard">
              <button className="w-full bg-blue-600 text-white py-2 rounded">Login</button>
            </a>
          </div>

          <p className="text-xs text-gray-400">Need help? Contact your administrator.</p>
        </div>

        <div className="hidden md:flex items-center justify-center bg-blue-50">
          <div className="p-6">
            <Image src="/hero-illust" alt="hero" width={360} height={360} />
          </div>
        </div>
      </div>
    </div>
  );
}
